import React, { useEffect, useState } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Home from "../scenes/Home/Home";
import ProfileHome from "../scenes/profile/ProfileHome";
import Signin from "../scenes/auth/Signin/Signin";
import Signup from "../scenes/auth/Signup/Signup";
import { auth } from "../data/firebase";

const Router = () => {
  const [userName, setUserName] = useState("");
  const [userActive, setuserActive] = useState(true);

  useEffect(() => {
    auth.onAuthStateChanged((user) => {
      if (user) {
        setUserName(user?.displayName);
        setuserActive(true);
      } else {
        setUserName("");
      }
    });
  }, []);
  return (
    <BrowserRouter>
      <Routes>
        {userActive ? (
          <Route path="/musemeld/" element={<Signup />} />
        ) : (
          <Route path="/musemeld/" element={<ProfileHome name={userName} />} />
        )}{" "}
        <Route path="/musemeld/Home" element={<Home />} />
        <Route path="/musemeld/" element={<Signin />} />
        <Route path="/musemeld/signin" element={<Signin />} />
        <Route path="/musemeld/signup" element={<Signup />} />
        <Route
          path="/musemeld/Profile"
          element={<ProfileHome name={userName} />}
        />
      </Routes>
    </BrowserRouter>
  );
};

export default Router;
